* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "montseratt", sans-serif;
}

.app {
  background-image: url("../images/cold.jpg");
  background-size: cover;
  background-position: bottom;
  transition: 0.4s base-out;
}
.app.warm {
  background-image: url("../images/warm.jpg");
}
#cartersName {
  color: #523a43;
}

a:hover#cartersName {
  color: #f0ff03;
}
.foot {
  background-color: #191820;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: center;
  font-family: "Alegreya Sans SC", sans-serif;
  font-size: 18px;
  height: 13vh;
  padding: 45px;
  color: #3c3a4e;
  bottom: 0;
  left: 0;
  right: 0;
}
.foot.warm {
  background-color: #173555;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: center;
  font-family: "Alegreya Sans SC", sans-serif;
  font-size: 18px;
  height: 13vh;
  padding: 45px;

  bottom: 0;
  left: 0;
  right: 0;
}

.footer-basic-centered .footer-company-motto {
  color: #47627e;
  font-size: 24px;

  color: #950740;
  margin-bottom: 0px;
}
main {
  min-height: 87vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.75)
  );
  padding: 25px;
}
.center {
  display: flex;
  justify-content: center;
}
.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: flex;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #fff;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
@media (min-width: 1000px) {
  .search-box .search-bar {
    width: 80vw;
  }
  .search-box {
    width: 80vw;
  }
}
